import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import './Header.css'

const Header = () => {
    const [isOpen, setIsOpen] = useState(false)
    let menuStatus = isOpen ? 'isopen' : '';
    return (
        <header className="header">
            <NavLink to="/" className="logotype">
                <div className="logotype">
                    <img src={process.env.PUBLIC_URL + '/images/logo.svg'} alt="Logotype" />
                    <div className="text">
                        <span>vagan</span>
                        <span>mkrtchyan</span>
                    </div>
                </div>
            </NavLink>
            <nav className="navigation">
                <ul>
                    <li><NavLink to="/about" activeClassName="active" data-content="ABOUT">about</NavLink></li>
                    <li><NavLink to="/skills" activeClassName="active" data-content="SKILLS">skills</NavLink></li>
                    <li><NavLink to="/projects" activeClassName="active" data-content="PROJECTS">projects</NavLink></li>
                    <li><NavLink to="/contact" activeClassName="active" data-content="CONTACT ME">contact me</NavLink></li>
                </ul>
            </nav>
            <div className="menubar">
                <div className={`hambmenu ${menuStatus}`} onClick={() => setIsOpen(isOpen => !isOpen)}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
            <MenuLinks menuStatus={menuStatus} />
        </header>
    )
}
const MenuLinks = ({ menuStatus }) => {
    const arr = [{
        text: 'about',
        link: '/about'
    }, {
        text: 'skills',
        link: '/skills'
    }, {
        text: 'projects',
        link: '/projects'
    }, {
        text: 'contact me',
        link: '/contact'
    }]

    let links = arr.map((link, i) =>
        <li key={i}>
            <NavLink to={link.link}>{link.text}</NavLink>
        </li>);

    return (
        <div className={`${menuStatus} menu`}>
            <ul>
                {links}
            </ul>
        </div>
    )
}



export default Header